import React, {useContext, useEffect, useRef, useState} from "react";
import {MyBanner} from "../MyBanner";
import {CircularProgress, List, ListItem} from "@material-ui/core";
import ReactMarkdown from "react-markdown";
import {getStringAfter, MyButton, myMarkdownRenderers, useEndPoint} from "../../Util";
import {useTranslation} from "react-i18next";
import {useHistory, useLocation} from "react-router-dom";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import Typography from "@material-ui/core/Typography";
import LinkIcon from '@material-ui/icons/Link';
import IconButton from "@material-ui/core/IconButton";
import Divider from "@material-ui/core/Divider";

const gfm = require('remark-gfm')

const SetWikiTitleContext = React.createContext((title: string) => {
});

function processLink(url: string): string {
    return url.substr(url.indexOf('/wiki'))
}

function getLocalWikiUrlFromGithubWikiUrl(githubWikiUrl: string) {
    const processed = processLink(githubWikiUrl);
    return '/immptl' + processed;
}

function MySidebarLink(props: { url: string, children: any }) {
    const history = useHistory();

    const location = useLocation();

    var githubWikiUrl = props.url;
    var localUrl = getLocalWikiUrlFromGithubWikiUrl(githubWikiUrl);

    var context = useContext(SetWikiTitleContext);

    const selected = location.pathname === localUrl;

    useEffect(() => {
        if (selected) {
            context(props.children[0].props.value)
        }
    });

    return <div>
        <ListItem selected={selected} button onClick={() => {
            history.push(localUrl);
        }}>{props.children}</ListItem>
    </div>;
}

const sidebarMarkdownRenderers = {
    link: (a: any) => {
        return <MySidebarLink url={a.href}>{a.children}</MySidebarLink>;
    },
    paragraph: (a: any) => a.children
}

//both github wiki url and this website url
function getMdUrlFromWikiUrl(url: string) {
    const head = 'https://raw.githubusercontent.com/wiki/qouteall/ImmersivePortalsMod'

    var innerPath = getStringAfter(url, '/wiki');

    if (innerPath.length === 0) {
        return head + '/Home.md';
    }

    return head + innerPath + '.md';
}

function MyLinkInWiki(props: { targetLink: string, children: any }) {
    let history = useHistory();


    if (props.targetLink.indexOf('github.com/qouteall/ImmersivePortalsMod/wiki') !== -1) {
        //inner link

        return <MyButton onClick={() => {
            console.log(props.targetLink)
            var localWikiUrlFromGithubWikiUrl = getLocalWikiUrlFromGithubWikiUrl(props.targetLink);
            console.log(localWikiUrlFromGithubWikiUrl)
            history.push(localWikiUrlFromGithubWikiUrl)
        }} style={{
            padding: 0, margin: 0
        }}>
            {props.children}
        </MyButton>
    } else {
        return <a href={props.targetLink}>{props.children}</a>
    }
}

const toRemoveFromHash: RegExp = /[:<>]/g

function headingTextToHash(heading: string) {
    return '#' + heading.toLowerCase()
        .replaceAll(' ', '-')
        .replaceAll(toRemoveFromHash, '');
}

function WikiHeading(props: { level: number, children: any }) {
    const myRef: React.MutableRefObject<any> = useRef()
    const location = useLocation()
    const history = useHistory()

    // const text = props.children[0].props.value;
    // @ts-ignore
    const text = props.children.map(e => e.props.value).join('');

    const headingHash = headingTextToHash(text);

    console.log(headingHash)

    useEffect(() => {
        if (location.hash) {
            console.log(headingHash)
            if (location.hash === headingHash) {
                myRef.current.scrollIntoView({behavior: 'smooth'});
            }
        }
    })

    return <div style={{display: "flex", flexDirection: "row", alignItems: "stretch"}}>
        <div style={{
            display: "inline-flex", width: 5, background: "#ed4b82",
            marginTop: 2, marginBottom: 2, marginRight: 10,
            flexShrink: 0
        }}/>
        <Typography ref={myRef} style={{fontSize: 40 - props.level * 5}}>
            {props.children}
            <IconButton onClick={() => {
                history.push(headingHash)
            }}>
                <LinkIcon/>
            </IconButton>
        </Typography>
    </div>;
}

const wikiMarkdownRenderers = {
    ...myMarkdownRenderers,
    table: (a: any) => {
        return <Table>{a.children}</Table>
    },
    tableHead: (a: any) => <TableHead>{a.children}</TableHead>,
    tableRow: (a: any) => <TableRow>{a.children}</TableRow>,
    tableBody: (a: any) => <TableBody>{a.children}</TableBody>,
    tableCell: (a: any) => <TableCell>{a.children}</TableCell>,
    link: (a: any) => {
        return <MyLinkInWiki targetLink={a.href}>{a.children}</MyLinkInWiki>
    },
    heading: (a: any) => {
        const level: any = a.level;

        return <WikiHeading level={level}>{a.children}</WikiHeading>
    }
}


export function IPWikiPage() {
    let {t, i18n} = useTranslation();

    let location = useLocation();

    const {data: sideBarData, complete: complete} = useEndPoint(
        t('url_wiki_sidebar')
    );

    var wikiContentUrl = getMdUrlFromWikiUrl(location.pathname);

    const {data: wikiData, complete: wikiResultComplete} = useEndPoint(wikiContentUrl);

    const [wikiTitle, setWikiTitle] = useState('');

    return <div className='My-Page' style={{display: 'flex', flexDirection: 'column'}}>
        <div style={{height: '100vh', maxHeight: '100vh', display: 'flex', flexDirection: 'column'}}>


            <MyBanner text={'Immersive Portals Wiki'} color='#9500ae'/>
            <div style={{
                display: 'flex', flexDirection: 'row', flexGrow: 1,
                flexShrink: 1,

                flexBasis: 0, minHeight: 0 //fuck flex
                //https://stackoverflow.com/questions/31867316/how-can-i-force-a-nested-flexbox-element-to-shrink-and-display-a-scrollbar-when
            }}>
                <SetWikiTitleContext.Provider value={setWikiTitle}>
                    <List component='nav'
                          style={{
                              minWidth: '20vw',
                              maxWidth: '20vw',
                              overflowY: 'auto'
                          }}>
                        {
                            complete ?
                                <ReactMarkdown renderers={sidebarMarkdownRenderers}>
                                    {sideBarData || 'error'}
                                </ReactMarkdown>
                                : <CircularProgress/>
                        }
                    </List>
                </SetWikiTitleContext.Provider>

                <div style={{
                    margin: 0, overflowY: 'auto',
                    flexGrow: 1, padding: 20
                }}>
                    <Typography variant='h4'>{wikiTitle}</Typography>
                    <Divider style={{margin:10}}/>
                    {wikiResultComplete ?
                        <ReactMarkdown renderers={wikiMarkdownRenderers} plugins={[gfm]}>
                            {wikiData || 'error'}
                        </ReactMarkdown>
                        : <CircularProgress/>}
                </div>
            </div>
        </div>

    </div>;
}